<template>
	<section>
		<div v-for="log in logs" :key="log.createdAt">{{log.user}} | {{log.action}}</div>
	</section>
</template>

<script>
export default {
	data() {return {
		rawLogs: []
	}},
	created() {
		fetch('/logs')
		.then(res => res.json())
		.then(json => {
			this.rawLogs = json
			console.log(json)
		})
	},
	computed: {
		logs() {

		}

	}
}
</script>

<style lang="scss" scoped>
	section {
		background: #fff;
	}

	
</style>



